<template>

    <div class="main-content">
        <div class="breadcrumbs" id="breadcrumbs">
            <ul class="breadcrumb">
                <li>
                    <i class="icon-home home-icon"></i>
                    <a href="#"></a>
                </li>
                <li class="active">微服务</li>
                <li class="active">办事指南</li>
            </ul><!-- .breadcrumb -->
        </div>
        <div class="cs-filter-box cs-searchdiv">
            <!-- 筛选 -->
            <el-button-group>
                <el-button :plain="flag != 0" type="primary" @click="filterTag(0)">未读</el-button>
                <el-button :plain="flag != 1" type="primary" @click="filterTag(1)">已读</el-button>
                <el-button :plain="flag != 2" type="primary" @click="filterTag(2)">全部</el-button>
            </el-button-group>
            <div class="cs-searchBox " >
                <input type="text" placeholder="来信人" autocomplete="off" class="el-input__inner cs-search_input" @keyup.enter="onEnter"  @blur="onEnter" v-model="searchBoxtext">
                <img src="../../img/lookup.png" @click="onEnter" alt="搜索">
            </div>
        </div>
        <div class="page-content" id="page-content">
            <div class="contBox clear">
                <div class="col-sm-6 no-padding-left" id="bszn_left">
                    <el-div>
                        <el-table
                    		    :data="tableData"
                    		    border
                    		    height="390"
                    		    style="width: 100%">
                    		    <el-table-column
                    		      type="selection"
                    		      width="55">
                    		    </el-table-column>
                    		    <el-table-column
                    		      prop="userName"
                    		      label="来信人姓名"
                    		      width="120">
                    		    </el-table-column>
                    		    <el-table-column
                    		      prop="number"
                    		      label="联系电话"
                    		      width="130">
                    		    </el-table-column><el-table-column
                    		      prop="date"
                              sortable
                    		      label="来信时间"
                    		      width="180">
                    		    </el-table-column>
                    		    <el-table-column
                    		      prop="title"
                    		      label="信件标题"
                    		      width="225">
                    		    </el-table-column>
                    		    <el-table-column
                    		      prop="content"
                    		      label="信件内容"
                              class="cs-conter"
                    		      width="260">
                    		    </el-table-column>
                    		    <el-table-column
                    		      label="新增"
                    		      width="150">
                    		      <template scope="scope">
                    		        <el-button @click="lookClick(scope.$index)" type="text" size="small">查看</el-button>
                    		        <el-button @click="replyClick(scope.$index)" type="text" size="small">回复</el-button>
                    		        <el-button @click="deleteClick(scope.$index)" type="text" size="small">删除</el-button>
                    		      </template>
                            </el-table-column>
                    	  </el-table>
                    	  <el-pagination
                      	      @size-change="handleSizeChange"
                              @current-change="handleCurrentChange"
                              :current-page="currentPage4"
                              :page-sizes="[10, 20, 30, 40]"
                              :page-size="10"
                              layout="total, sizes, prev, pager, next, jumper"
                              :total="400">
                    	  </el-pagination>
                    </el-div>
                </div>
                    
                <div id="openinfo" class="col-sm-6 no-padding-right">
                    <el-button class="cs-quxiao" @click="quxiao" size="mini">X</el-button>
                    <h2>所长信箱</h2>
                    <p><b>来信人姓名：</b>{{selectRowData.realname}}  <b>联系电话：</b>{{selectRowData.number}}   <b>来信人时间：</b>{{selectRowData.date}}</p>
                    <span><b>来信标题</b>：{{selectRowData.title}}</span>
                    <p><b>来信内容：</b>{{selectRowData.content}}</p>
                    <p></p>
                    <el-input
                        type="textarea"
                        :autosize="{ minRows: 7, maxRows: 11}"
                        placeholder="请输入回复内容"
                        v-model="textarea3">
                    </el-input>
                    <div class="goback cs-back">
                        <el-button type="primary" @click="backClick" >回复</el-button>
                        <el-button type="primary" @click="deteleClick" >返回</el-button>
                    </div>
                </div>
            </div>
        </div>
    </div>

</template>


<script>

  export default {
    methods: {
          lookClick(index) {
              this.numID=this.tableData[index].id;
              this.oisRead=false;
              this.selectRowData = this.tableData[index];
              var  bszn_left=document.getElementById('bszn_left');
              var bszn_right=document.getElementById('openinfo');
              bszn_right.style.display="block";
              bszn_left.style.width="50%";
          },
          replyClick(index) {this.numID=this.tableData[index].id;
              this.selectRowData = this.tableData[index];
              var bszn_left=document.getElementById('bszn_left');
              var bszn_right=document.getElementById('openinfo');
              bszn_right.style.display="block";
              bszn_left.style.width="50%";
            },
            deleteClick(index) {
                  let that = this;
                  this.numID=this.tableData[index].id;
                  this.$confirm('此操作将删除该信件, 是否继续?', '提示', {
                  confirmButtonText: '确定',
                  cancelButtonText: '取消',
                  type: 'warning'
                }).then((ref) => {
                  if (ref) {   //删除留言
                      this.$http({
                          method:'POST',
                          url:global.config.weburl +"api/msg/delete.do",
                          params:{token:this.otoken,id:this.numID}
                      }).then(
                          function(res){
                            if(res.data.code==0){
                              this.$message({
                                  type: 'info',
                                  message: '删除成功'
                              });
                            } else {
                                global.tools.handleError(res.data);
                            }
                          }
                      );
                    }
                }).catch(() => {
                  this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
              });
                //查询所长信箱
                 this.$http({
                    method:'GET',
                    url:global.config.weburl +"api/msg/list.do",
                    params:{token:this.token,type:2,page:1,pageSize:10}
                }).then(
                    function(res){
                        if (res.data.code == 0) {
                            that.tableData=res.data.data;
                        } else {
                            global.tools.handleError(res.data);
                        }

                    }
                );
            },
            saveClick() {
              console.log(1);
              var bszn_left=document.getElementById('bszn_left');
              var bszn_right=document.getElementById('openinfo');
              bszn_right.style.display="none";
              bszn_left.style.width="100%";
            },
            releaseClick() {
                console.log(1);
                var bszn_left=document.getElementById('bszn_left');
                var bszn_right=document.getElementById('openinfo');
                bszn_right.style.display="none";
                bszn_left.style.width="100%";
            },
            quxiao(){
              var bszn_left=document.getElementById('bszn_left');
              var bszn_right=document.getElementById('openinfo');
              bszn_right.style.display="none";
              bszn_left.style.width="100%";
            },
            deteleClick(){
              var bszn_left=document.getElementById('bszn_left');
              var bszn_right=document.getElementById('openinfo');
              bszn_right.style.display="none";
              bszn_left.style.width="100%";
            },

            backClick(){   //留言回复
              let userinfo=sessionStorage.getItem('ha_ao_userinfo');
              let userID=(JSON.parse(userinfo).id);
                this.$http({
                      method:'POST',
                      url:global.config.weburl +"api/msg/reply.do",
                      params:{token:this.token,id:this.numID,replyUserId:userID,replyContent:this.textarea3}
                  }).then(
                      function(res){
                        console.log(res)
                        if(res.data.code==0){
                          this.$message('回复成功！');
                        }else{
                          this.$message({
                            message: '回复失败！ 回复不能为空',
                            type: 'warning'
                          });
                        }
                      }
                  );
                  if(!this.oisRead){
                    this.$http({
                        method:'GET',
                        url:global.config.weburl +"api/msg/list.do",
                        params:{token:this.otoken,type:2,page:1,pageSize:10,isRead:0}
                    }).then(
                        function(res){
                          console.log(res)
                          this.tableData=res.data.data;  
                        }
                    );
                    this.oisRead=true;
                  }
              this.textarea3="";
              var bszn_left=document.getElementById('bszn_left');
              var bszn_right=document.getElementById('openinfo');
              bszn_right.style.display="none";
              bszn_left.style.width="100%";
            },
            addywClick(){
              var add=document.getElementById('add');
              var addyw=document.getElementById('addyw');
              addyw.style.display="inline";
              var btn=document.createElement("button").value=addyw.value
            },
            handleSizeChange(val) {
              console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
              this.currentPage = val;
              console.log(`当前页: ${val}`);
            },
            filterTag(type) {
                  this.flag = type;
                  this.$http({
                      method:'GET',
                      url:global.config.weburl +"api/msg/list.do",
                      params:{token:this.token,type:2,page:1,pageSize:10,isRead:type}
                  }).then(
                      function(res){
                        console.log(res)
                        this.tableData=res.data.data;  
                      }
                  );
                  if(type==2){
                    this.$http({
                          method:'GET',
                          url:global.config.weburl +"api/msg/list.do",
                          params:{token:this.token,type:2,page:1,pageSize:10}
                      }).then(
                          function(res){
                            console.log(res)
                            this.tableData=res.data.data;  
                          }
                      );
                  }
            },

            onEnter(){  //筛选框失去焦点/回车/点击搜索开始筛选
              if(this.searchBoxtext.length>1){
                    //发送筛选数据
                    this.$http({
                        method:'GET',
                        url:global.config.weburl +"api/msg/list.do",
                        params:{token:this.token,type:2,page:1,pageSize:10,userName:this.searchBoxtext}
                    }).then(
                        function(res){
                          this.tableData=res.data.data;  
                        }
                    );
              }else{
                  //查询所长信箱
                  this.$http({
                      method:'GET',
                      url:global.config.weburl +"api/msg/list.do",
                      params:{token:this.token,type:2,page:1,pageSize:10}
                  }).then(
                      function(res){
                        console.log(res)
                        this.tableData=res.data.data;  
                      }
                  );
              }
            }
    },
    data() {
      return {
        textarea2: '',
        textarea3: '',
        input6:'',
        otoken:'',
        tableData: [],
        numID:'',
        memberlist:[],
        arrtable:[],
        userID:'',
        flag:0,
        searchBoxtext:'',
        selectRowData:[],
        currentPage4: 4,
        oisRead:true
      }
    },
     created:function(){
          let userinfo=sessionStorage.getItem('ha_ao_userinfo');
          let userID=(JSON.parse(userinfo).id);
          let ken=sessionStorage.getItem('ha_ao_token');
          this.otoken=ken;
         let that = this;
        // let token = global.data.getToken();
         if(!ken) {
             return false;
         }
        this.$http({
            method:'GET',
            url:global.config.weburl +"api/msg/list.do",
            params:{token:ken,type:2,page:1,pageSize:10}
        }).then(
            function(res){
              console.log(res)
              this.tableData=res.data.data;  
            }
        );
    }
  }
</script>
<style>
.el-pagination{width: 500px;float: right;margin-right: 20%; margin-top: 10px;}
.cs-conter{overflow: hidden!important;white-space: nowrap!important;text-overflow: ellipsis!important;width:270px;}
.cs-filter-box{margin:5px 10px;}
.cs-searchBox{ height: 40px;}
.cs-searchBox input{float: left;}
.cs-searchBox img{float: right; margin-top: -30px;}
.cs-searchdiv{justify-content:space-between;}
.goback{width: 100%; text-align: center; }
.cs-quxiao{float: right;position: relative;right: 20px; top: -10px;}
.el-pagination{width: 70%;}
</style>




